<template>
  <div>
    <el-row>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>
          <a href="/">任务列表</a>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="mt15" :gutter="5">
      <el-col :xl="1" :lg="1" :md="3" :sm="4" :xs="4">
        <el-button size="mini" type="success" @click="reload">刷新</el-button>
      </el-col>
      <el-col :xl="1" :lg="1" :md="3" :sm="4" :xs="4">
        <el-button size="mini" type="warning" @click="$router.push('/admin/task/add')">新增</el-button> 
      </el-col>
    </el-row>
    <el-row class="mt15" :gutter="10">
      <el-col :span="7">
        <el-date-picker
          v-model="date_time"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
      </el-col>

      <el-col :span="3">
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="3">
        <el-input placeholder="任务名称"></el-input>
      </el-col>
      <el-col :span="2">
        <el-button icon="el-icon-search" @click="searchBtn">查询</el-button>
      </el-col>
    </el-row>
    <el-table class="mt15" :data="tableData" border style v-loading="loading">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" label="编号" fixed width="60" align="center" header-align="center"></el-table-column>
      <el-table-column label="id" prop="id" fixed align="center" header-align="center"></el-table-column>
      <el-table-column fixed prop="title" label="标题" align="center" header-align="center"></el-table-column>
      <el-table-column label="logo" align="center" header-align="center">
        <template slot-scope="props" >
          <img :src="props.row.logo" style="width:80px;height:80px;">
        </template>
      </el-table-column>
      <el-table-column prop="status" label="是否指定" width="120" align="center" header-align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.is_specified_people == 1 ">
            已指定：{{ scope.row.specified_people_list.length }} 人
          </div>
          <div v-else >
            普通任务
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态" width="120" align="center" header-align="center">
        <template slot-scope="scope">
          <el-button type="success" v-if="scope.row.status==1" size="mini">上架中</el-button>
          <el-button type="error" v-if="scope.row.status==2" size="mini">已下架</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="city" label="审核状态" width="120" align="center" header-align="center">
        <template slot-scope="scope">
          <el-button type="success" v-if="scope.row.audit_ret==1" size="mini">审核成功</el-button>
          <el-button type="warning" v-if="scope.row.audit_ret==3" size="mini">审核中</el-button>
          <el-button type="error" v-if="scope.row.audit_ret==2" size="mini">审核失败</el-button>
        </template>
      </el-table-column> 
      <el-table-column label="报名人数" width="120" align="center" header-align="center">
        <template slot-scope="scope">
           <el-button  @click="showSign(scope.row.id,1)" size="mini" type="text">{{ scope.row.sign_count ? scope.row.sign_count : 0}}</el-button>
        </template>
      </el-table-column>
      <el-table-column label="金额" align="center" header-align="center">
        <template slot-scope="scope">
          {{ scope.row.type == 1 ? '每单' : '每小时'}} {{scope.row.money}}
        </template>
      </el-table-column>
      <el-table-column prop="end_time" label="截止日期" align="center" header-align="center">
      </el-table-column>
       <el-table-column prop="created_at" label="创建日期" align="center" header-align="center">
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="200" height="200">
        <template slot-scope="scope">
          <el-button type="text" size="mini">编辑</el-button>
          <el-button type="text" size="mini" @click="del(scope.row.id)">删除</el-button>
          <el-button type="text" size="mini" @click="showSign(scope.row.id,3)" class="bm">报名列表<span style="color:red;">({{ scope.row.sign_ing > 0 ? scope.row.sign_ing : 0}})</span></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="mt15">
      <el-pagination layout="prev, pager, next" :total="total" :page-size="limit" @current-change="pageChange" :background="true"></el-pagination>
    </div>
    <el-dialog
        title="报名人员"
        :visible.sync="signVisible"
        width="700px"
        :center="true"
        :close-on-click-modal="false"
      >
        
        <el-table :data="signData">
          <el-table-column property="nickname"  label="名字" width="150" align="center" header-align="center"></el-table-column>
          <el-table-column property="name" label="姓名" width="200" align="center" header-align="center">
            <template slot-scope="scope">
              <img :src="scope.row.logo" width="40" height="40">
            </template>
          </el-table-column>
          <el-table-column property="phone"  label="手机" width="150" align="center" header-align="center"></el-table-column>
          <el-table-column  label="操作" align="center" header-align="center">
            <template slot-scope="scope">
              <div v-if="scope.row.status==3">
                <el-button size="mini" type="success" @click="signUser(scope.row.uid, scope.row.task_id, 1, scope.$index)">同意</el-button>
                <el-button size="mini" type="danger" @click="signUser(scope.row.uid, scope.row.task_id, 2,scope.$index)">拒绝</el-button>
              </div>
              <div v-if="scope.row.status!=3">
                <el-button size="mini" type="text">已同意</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>

      </el-dialog>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      signVisible : false,
      signData:[],
      search : {
      },
      date_time: "",
      value: 0,
      options:[1],
      total: 1000,
      limit: 10,
      page:1,
      loading:true,
      tableData: [
        
      ]
    };
  },
  created(){
     this.loadData({});
  },
  mounted() {
    this.loading = false
  },
  methods: {
    signUser(uid , id , r,index){
      console.log(index)
      let data = {
        uid : uid,
        task_id : id,
        status : r,
      };
      this.$api.post('/enterprise/task/edit_user_sign',data , res=> {
        if( res.data.bol )
        {
          this.$message.success(res.data.msg)
          this.showSign(id, 3)
        }else{
          this.$message.error(res.data.msg)
        }
      })
    },
    showSign(id, status){
      let self = this;
      let data = {
        id: id,
        status: status
      };
      this.$api.get('/enterprise/task/sign_data',data , res=> {
        let data = res.data.data;
        if( data.total <= 0 )
        {
          this.$message.error('暂无报名');
          self.signVisible= false;
        }else{
          self.signData=data.list;
          self.signVisible= true;
        }
      })
    },
    loadData (search) {
      console.log(search)
      let sea = search ? search : {};
      sea.page=this.page;
      sea.limit=this.limit
      this.$api.get('enterprise/task/list',sea, res=>{
        this.tableData=res.data.data.list
        this.total = res.data.data.total
      });
    },
    dateRes (date) {
        var d = new Date(date);  
        var  resDate = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds(); 
        return resDate;
    },
    searchBtn(){
      var data = {
        begin_time:this.date_time[0],
        end_time:this.date_time[1],
      };
      this.search=data;
      this.loadData(data)
    },
    handleClick(row) {
      console.log(row);
    },
    del(id){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$api.post('/enterprise/task/del', {id : id}, res=> {
            if(res.data.bol)
            {
              this.$message.success(res.data.msg);
              this.loadData({});
            }else{
              this.$message.error(res.data.msg);
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    pageChange(e)
    {
      this.page=e;
      this.loadData({});
    },
    reload()
    {
      window.localtion.reload();
    }
  }
};
</script>

<style>
  
</style>



